		this.capital.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.CAPITAL;
		this.patrimonioNeto.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.PATRIMONIO_NETO;
		this.totalPasivoYPatrimonio.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.TOTAL_PASIVO_Y_PATRIMONIO;
		this.ingresosOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.INGRESOS_OPERACIONALES;
		this.costoDeVentas.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.COSTO_DE_VENTAS;
		this.utilidadBruta.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.UTILIDAD_BRUTA;
		this.gastosOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.GASTOS_OPERACIONALES;
		this.utilidadOperacional.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.UTILIDAD_OPERACIONAL;
		this.ingresosNoOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.INGRESOS_NO_OPERACIONALES;
		this.gastosNoOperacionales.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.GASTOS_NO_OPERACIONALES;
		this.impuestosYComplemetarios.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.IMPUESTOS_Y_COMPLEMETARIOS;
		this.gananciasYPerdidas.vIdInfoTypeFinantial = CatalogFinantialInfoEnum.GANANCIAS_Y_PERDIDAS;
