	private mappingToBranchTemp() {
		this.listCities=JSON.parse(sessionStorage.getItem('listCities'));
		this.branchTemps = [];
		this.branchModels.forEach(branchModel => {	
			var branchTemp = new BranchTemp();
			branchTemp.id= Math.floor((Math.random() * 100000) + 1);
			branchTemp.nombre = branchModel.branch.vName;
						
			branchModel.branchInfos.forEach(info => {				
				switch (info.vIdInfoTypeBranch) {
					case CatalogBranchInfoEnum.CIUDAD:
						branchTemp.ciudad = this.getCity(info.vValue); 
						break;
					case CatalogBranchInfoEnum.DIRECCION:
						branchTemp.direccion = info.vValue;
						break;
					case CatalogBranchInfoEnum.TELEFONO:
						branchTemp.telefono = info.vValue;
						break;
				}				
			});
			this.branchTemps.push(branchTemp);
		});
	}
	
	private getCity(code : string){
		var itemCiudad=this.listCities.filter(item => item.codHijo == code);
		return (itemCiudad!=null && itemCiudad.length>0) ? itemCiudad[0] : { "codPadre": "aa", "nomPadre": "aa", "codHijo": "aa", "nomHijo": "aa" };
	}

	private mappingToServiceTemp() {

		this.serviceTemps = [];
		this.servicesModels.forEach(serviceModel => {	
			var serviceTemp = new ServiceTemp();		
			serviceTemp.id= Math.floor((Math.random() * 100000) + 1);
			serviceModel.serviceInfos.forEach(item => {				
				serviceTemp.service = item.vValue;				
			});
			this.serviceTemps.push(serviceTemp);
		});
	}


	upsertInfo() {
		this.ciudadMunicipio.vvalue=this.ciudadThird.codHijo;
		//upsertProfileBasic
		var basic = new ProfileBasic();
		this.mappingToBranch();
		this.mappingToService();

		basic.third = this.third;
		basic.thirdInfos = [
			this.nombreComercial,
			this.ciudadMunicipio,
			this.camaraComercio,
			this.estadoMatricula,
			this.actividadEconomica,
			this.codigoCiiu,
			this.descripcionEmpresa,
			this.correoElectronico,
			this.telefono,
			this.direccionComercial,
			this.paginaWeb,
			this.facebook,
			this.twitter,
			this.instagram,
			this.linkedin
		];
		basic.idUserThirdPublic = this.idUserThirdPublic;
		basic.branchModels = this.branchModels;
		basic.servicesModels = this.servicesModels;

		this.profileBasicUpsert.create(basic).subscribe(response => {
			console.log(response);
			//			this.fillOutInformation(response);
		});
	}
	private callBasicInformation() {
		this.profileBasic.sendRequest(this.userProfileRequest).subscribe(response => {
			this.fillOutInformation(response);
		});
	}

	private fillOutInformation(response: RootObject) {
		this.listCities=JSON.parse(sessionStorage.getItem('listCities'));
		
		let profileBasicResponse: ProfileBasic;
		profileBasicResponse = response.content;
		
		this.branchModels = profileBasicResponse.branchModels;
		this.servicesModels = profileBasicResponse.servicesModels;
		this.third = profileBasicResponse.third;
		this.idUserThirdPublic = profileBasicResponse.idUserThirdPublic;

		profileBasicResponse.thirdInfos.forEach(thirdInfo => {
			switch (thirdInfo.vidInfoTypeThird) {
				case CatalogThirdInfoProfileEnum.RAZON_SOCIAL_NOMBRE: { this.nombreComercial = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.CIUDAD__MUNICIPIO: { 
					this.ciudadMunicipio = thirdInfo; 
					this.ciudadThird = this.getCity(this.ciudadMunicipio.vvalue);					
					break;					
				}
				case CatalogThirdInfoProfileEnum.CAMARA_DE_COMERCIO: { this.camaraComercio = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.ESTADO_DE_LA_MATRICULA: { this.estadoMatricula = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.ACTIVIDAD_ECONOMICA: { this.actividadEconomica = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.CODIGO_CIIU: { this.codigoCiiu = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.DESCRIPCION_DE_LA_EMPRESA: { this.descripcionEmpresa = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.CORREO_ELECTRONICO: { this.correoElectronico = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.TELEFONO: { this.telefono = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.DIRECCION_COMERCIAL: { this.direccionComercial = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.PAGINA_WEB: { this.paginaWeb = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.FACEBOOK: { this.facebook = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.TWITTER: { this.twitter = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.INSTAGRAM: { this.instagram = thirdInfo; break; }
				case CatalogThirdInfoProfileEnum.LINKEDIN: { this.linkedin = thirdInfo; break; }
			}
		});


		if (this.branchModels != null && this.branchModels.length > 0) {
			this.mappingToBranchTemp();
		} else {
			this.addBranchTemp();
		}

		if (this.servicesModels != null && this.servicesModels.length > 0) {
			this.mappingToServiceTemp();
		} else {
			this.addServiceTemp();
		}

	}
