import { Component, OnInit, Inject } from '@angular/core';
import { ProfileBasicToken } from "../../../services/profile/profilebasic.provider";
import { ProfileRequest } from "../../../../../domain/models/profile/profilerequest.model";
import { ProfileBasic } from "../../../../../domain/models/profile/profilebasic.model";
import { IRepository } from '../../../../../domain/interfaces/irepository.interface';
import { Third } from "../../../../../domain/models/register/third.model";
import { User } from "../../../../../domain/models/register/user.model";
import { ThirdInfo } from "../../../../../domain/models/register/thirdInfo.model";
import { BranchModel } from "../../../../../domain/models/profile/branchmodel.model";
import { ServiceModel } from "../../../../../domain/models/profile/servicemodel.model";
import { RootObject } from "../../../../../domain/models/register/rootobject.model";
import { CatalogThirdInfoProfileEnum } from '../../../../../domain/enums/CatalogThirdInfoProfileEnum';
import { CatalogBranchInfoEnum } from '../../../../../domain/enums/CatalogBranchInfoEnum';
import { CatalogServiceInfoEnum } from '../../../../../domain/enums/CatalogServiceInfoEnum';

import { ProfileBasicService } from "../../../services/profile/profilebasic.service";
import { Service } from "../../../../../domain/models/profile/service.model";
import { ServiceInfo } from "../../../../../domain/models/profile/serviceinfo.model";
import { Branch } from "../../../../../domain/models/profile/branch.model";
import { BranchInfo } from "../../../../../domain/models/profile/branchinfo.model";
import { CommunicationService } from "../../../services/Communication.service";
import { Example } from "../../../../../domain/models/example/RootObject";
import { branchOffice } from "../../../../../domain/models/profile/branchOffice";
import { Router } from '@angular/router';



@Component({
	selector: 'app-detail-profile',
	templateUrl: './detailprofile.component.html',
	styleUrls: ['./detailprofile.component.scss']
})
export class DetailProfileComponent implements OnInit {

	public userProfileRequest = new ProfileRequest();
	public score: string;
	public third = new Third();
	public comercialName = new ThirdInfo();
	public paginaWeb = new ThirdInfo();
	public correoElectronico = new ThirdInfo();
	public direccionComercial = new ThirdInfo();
	public twitter = new ThirdInfo();
	public instagram = new ThirdInfo();
	public facebook = new ThirdInfo();
	public telefono = new ThirdInfo();
	public actividadEconomica = new ThirdInfo();
	public descripcionEmpresa = new ThirdInfo();

	public ciudadMunicipio = new ThirdInfo();
	public branchModels: BranchModel[];
	public servicesModels: ServiceModel[];
	public servicesInfo: ServiceInfo[];

	public typeBranchTel: string;
	public typeBranchDir: string;
	public sucursales: branchOffice[]=[];
	/**Infos Third */


	constructor(@Inject(ProfileBasicToken) private profileBasic: IRepository<ProfileRequest>, public communication: CommunicationService, private router: Router) {
		this.userProfileRequest = this.loadSessionProfile();
		this.typeBranchTel = CatalogBranchInfoEnum.TELEFONO;
		this.typeBranchDir = CatalogBranchInfoEnum.DIRECCION;

		this.callBasicInformation();
		this.loadObject();
	}
	openProfile() {
		debugger
	this.router.navigate(['profile/home/edit']); 
	}
	loadObject() {
		this.third.vBusinessName = "DENTISALUD SAS";
		this.third.vIdentificationNumber = "900415240";
		this.ciudadMunicipio.vvalue = "Bogota";
		this.paginaWeb.vvalue = "www.dentisalud.com.co";
		this.correoElectronico.vvalue = "gerencia@dentisalud.com";
		this.direccionComercial.vvalue = "Calle 92#7 - 25";
		this.twitter.vvalue = "@dentisalud";
		this.instagram.vvalue = "@dentisalud";
		this.facebook.vvalue = "@dentisalud";
		this.telefono.vvalue = "300415685";
		this.descripcionEmpresa.vvalue = "Somos una compaia dedicada a ofrecer servicios de salud oral en la ciudad de Bogota, contamos con dos sedes en el Norte de Bogota y mas de 15 especialidades.";



		var serviceI_a = new ServiceInfo();
		serviceI_a.vValue = "Salud Oral";
		var serviceI_b = new ServiceInfo();
		serviceI_b.vValue = "Dental";
		var serviceI_c = new ServiceInfo();
		serviceI_c.vValue = "Cirugia"
		var serviceI_d = new ServiceInfo();
		serviceI_d.vValue = "Tratamientos";

		this.servicesInfo = [serviceI_a, serviceI_b, serviceI_c, serviceI_d];

		var branc_a = new Branch();
		branc_a.vName = "Chapinero";
		var branchIA_a = new BranchInfo();
		branchIA_a.vValue = "Calle 63# 13 - 22";
		branchIA_a.vIdInfoTypeBranch = "303002";
		var branchIA_b = new BranchInfo();
		branchIA_b.vValue = "3004523652";
		branchIA_b.vIdInfoTypeBranch = "303003";

		var branc_b = new Branch();
		branc_b.vName = "Chapinero";
		var branchIB_a = new BranchInfo();
		branchIB_a.vValue = "Calle 63# 13 - 22";
		branchIB_a.vIdInfoTypeBranch = "303002";
		var branchIB_b = new BranchInfo();
		branchIB_b.vValue = "3004523652";
		branchIB_b.vIdInfoTypeBranch = "303003";
		this.branchModels = [
			{
				branch: branc_a,
				branchInfos: [branchIA_a, branchIA_b]
			},
			{
				branch: branc_b,
				branchInfos: [branchIB_a, branchIB_b]
			}
		];

		this.branchModels.forEach(element => {
			var sucursal = new branchOffice();
			sucursal.sucursal = element.branch.vName;
			element.branchInfos.forEach(elementInfo => {
				switch (elementInfo.vIdInfoTypeBranch) {
					case this.typeBranchDir:
						sucursal.direccion = elementInfo.vValue;
						break
					case this.typeBranchTel:
						sucursal.telefono = elementInfo.vValue;
						break

				}
			});
			this.sucursales.push(sucursal);
		});
	}



	loadSessionProfile() {

		let userProfileRequest = new ProfileRequest()
		let user = new User();
		user.vIdUserPublic = "0bd54c4f-a4af-11e9-b586-0800278d7df1";
		user.vIdType = "101002";
		user.vIdentificationNumber = "7180224115";
		user.vName = "Diego";
		user.vLastName = "David";
		user.vEmail = "d_david9405@outlook.com";
		user.vPhone = "31098802491";
		user.vProfilePicture = "foto url mod";
		user.vEnable = 0;
		user.vIdTypeForListOrScore = "2";
		userProfileRequest.user = user;
		userProfileRequest.vIdThirdPublic = "0bbdbe34-a4af-11e9-b586-0800278d7df1";
		return userProfileRequest;
	}
	OnInit() {

	}
	public valorOBS: string;
	ngOnInit() {
		this.communication.obs_updateTransaction.subscribe(
			(resp: Example) => this.mapUpdateTransaction(resp),
			(errno) => console.log('error obs !')
		);
	}
	mapUpdateTransaction(response: Example) {
		this.valorOBS = response.title;
	}
	callBasicInformation() {
		this.profileBasic.sendRequest(this.userProfileRequest).subscribe(response => {
			this.fillOutInformation(response);
		});
	}





	fillOutInformation(response: RootObject) {
		let profileBasicResponse: ProfileBasic;
		profileBasicResponse = response.content;
		this.branchModels = profileBasicResponse.branchModels;
		this.servicesModels = profileBasicResponse.servicesModels;
		this.third = profileBasicResponse.third;
		this.score = profileBasicResponse.score;

		profileBasicResponse.thirdInfos.forEach(thirdInfo => {
			switch (thirdInfo.vidInfoTypeThird) {


				case CatalogThirdInfoProfileEnum.PAGINA_WEB: {
					this.paginaWeb = thirdInfo;
					break;
				}

				case CatalogThirdInfoProfileEnum.CORREO_ELECTRONICO: {
					this.correoElectronico = thirdInfo;
					break;
				}

				case CatalogThirdInfoProfileEnum.DIRECCION_COMERCIAL: {
					this.direccionComercial = thirdInfo;
					break;
				}

				case CatalogThirdInfoProfileEnum.TWITTER: {
					this.twitter = thirdInfo;
					break;
				}
				case CatalogThirdInfoProfileEnum.FACEBOOK: {
					this.facebook = thirdInfo;
					break;
				}


				case CatalogThirdInfoProfileEnum.INSTAGRAM: {
					this.instagram = thirdInfo;
					break;
				}

				case CatalogThirdInfoProfileEnum.TELEFONO: {
					this.telefono = thirdInfo;
					break;
				}
				case CatalogThirdInfoProfileEnum.ACTIVIDAD_ECONOMICA: {
					this.actividadEconomica = thirdInfo;
					break;
				}
				case CatalogThirdInfoProfileEnum.DESCRIPCION_DE_LA_EMPRESA: {
					this.descripcionEmpresa = thirdInfo;
					break;
				}
				case CatalogThirdInfoProfileEnum.CIUDAD__MUNICIPIO: {
					this.ciudadMunicipio = thirdInfo;
					break;
				}
				case CatalogThirdInfoProfileEnum.RAZON_SOCIAL_NOMBRE: {
					this.comercialName = thirdInfo;
					break;
				}



			}
		});

	}

}
