import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';
import { AppSettings } from '../../../../domain/appSettings';
import {ProfileSpesific} from '../../../../domain/models/profile/profilespesific.model';


@Injectable()
export class ProfileSpesificUpSertService implements IRepository<ProfileSpesific> {

	constructor(private http: HttpClient) {
	}

	public create(values: ProfileSpesific): Observable<RootObject> {
		var request = this.http.post<RootObject>(`http://default-bepartners-dev.apps.appcanvas.net/servicebroker/v1/catalog/profile/upsertProfileEspecific/`, values);		
		return request;
	}

}

